import { useQuery } from 'react-query';
import { fetchChartData } from '../api';
import { useOutletContext } from 'react-router-dom';
import ReactApexCharts from 'react-apexcharts';
import styled from 'styled-components';
import { BeatLoader } from 'react-spinners';
import { useContext } from 'react';
import { ThemeContext } from '../App';

interface IParams {
  coinId: string;
  days: number;
}

const LoadingContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: ${(props) => props.theme.bgColor};
`;
const ErrorContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: ${(props) => props.theme.bgColor};
  padding: 0 20px;

  div:nth-child(1) {
    color: ${(props) => props.theme.accentColor};
    font-size: calc(20px + 0.7vw);
    height: 150px;
    padding-top: 70px;
  }

  div:nth-child(2) {
    color: ${(props) => props.theme.textColor};
    height: 70px;
    padding-top: 20px;
    font-size: calc(10px + 0.5vw);
  }
`;

const Chart = () => {
  const { coinId, days } = useOutletContext<IParams>();
  const { theme } = useContext(ThemeContext);

  const {
    isLoading: chartLoading,
    data: chartData,
    error: chartError,
  } = useQuery<number[][]>(['chart', coinId, days], () =>
    fetchChartData(`${coinId}`, days)
  );

  return (
    <>
      {chartLoading ? (
        <LoadingContainer>
          <BeatLoader color="#FDDE55" margin={30} size={20} />
        </LoadingContainer>
      ) : chartError === null ? (
        <ReactApexCharts
          type="candlestick"
          series={[
            {
              data:
                chartData?.map((price) => {
                  return { x: new Date(price[0]), y: price.slice(1) };
                }) || [],
            },
          ]}
          width={`100%`}
          height={`auto`}
          options={{
            xaxis: {
              type: 'datetime',
              labels: {
                datetimeFormatter: {
                  hour: 'HH:mm',
                },
                style: {
                  colors: theme === true ? 'white' : 'black',
                },
              },
            },
            yaxis: {
              labels: {
                style: {
                  colors: theme === false ? 'white' : 'black',
                },
              },
            },
          }}
        />
      ) : (
        <ErrorContainer>
          <div>Sorry, we can't seem to find the data you're looking for.</div>
          <div>
            CRYPTOCURRENCY  API   .   
            .
          </div>
        </ErrorContainer>
      )}
    </>
  );
};

export default Chart;
